.category {
    // margin-top: 30px;
    // padding-top: 40px;
}
.category__title {
    font-weight: 500;
    font-size: 40px;
    line-height: 90%;
    letter-spacing: -0.05em;
    color: #222B40;
    max-width: 640px;
}
.category__cards {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    margin-top: 40px;
}
.card__link-btn {
    border: none;
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    min-height: 200px;
    width: 100%;
    transition: all 0.3s ease 0s;
    &:hover, &:focus {
        cursor: pointer;
        background: #795BDA;
        .category__card-title {
            color: #fff;
        }
        .category__card-old {
            span {
                color: #FF5C8D;
            }
        
        }
    }
}
.category__card-title {
    font-weight: 500;
    font-size: 24px;
    line-height: 110%;
    letter-spacing: -0.06em;
    color: #222B40;
    text-align: left;
    margin-right: 10px;
}

.category__card-old {
    display: flex;
    align-items: center;
    span {
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        letter-spacing: -0.01em;
        color: #795BDA;
    }

}

@media screen and (max-width: 1280px) {
    .category__cards {
        grid-template-columns: 208px 208px 208px;
    }
    .category__card-title {
        margin-right: 0; 
        font-size: 22px;
    }
}

@media screen and (max-width: 744px) {
    .category__title {
        font-size: 32px;
    }
    .category__cards {
        grid-template-columns: 170px 170px;
        gap: 10px;
    }
    .category__card-title {
        font-size: 16px;
    }
    .card__link-btn {
        min-height: 180px;
    }
}